<template>
    <!-- 0324 正在做 subjectModal and layout -->
    <!-- todos: subjects 的配色 -->
    <!-- todos: subjects 按下的按鈕動畫 -->
    <MobileContent v-if="isMobile" />
    <template v-else>
        <MainContent />
        <AboutMe />
        <EducationExperience />
        <SkillsPortfolio />
        <ContactMe />
    </template>
</template>

<script setup>
import { ref, onMounted, onBeforeUnmount } from "vue";
import MobileContent from "./components/MobileContent";
import MainContent from "./components/MainContent.vue";
import AboutMe from "./components/AboutMe.vue";
import EducationExperience from "./components/EducationExperience.vue";
import SkillsPortfolio from "./components/SkillsPortfolio.vue";
import ContactMe from "./components/ContactMe.vue";

const isMobile = ref(false);

const checkMobile = () => {
    isMobile.value = window.matchMedia("(max-width: 768px)").matches;
};

onMounted(() => {
    checkMobile();
    window.addEventListener("resize", checkMobile);
});

onBeforeUnmount(() => {
    window.removeEventListener("resize", checkMobile);
});
</script>

<style>
body {
    margin: 0;
}

@font-face {
    font-family: Roboto;
    src: url('~@/assets/Roboto-Regular.ttf');
}

#app {
    font-family: monospace;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    display: flex;
    flex-direction: column;
    background-color: #050505;
    color: #ffffff;
    user-select: none;
}

#app>* {
    height: 100svh;
}

::-webkit-scrollbar {
    width: 8px;
}

::-webkit-scrollbar-track {
    background: #3f3f3f;
}

::-webkit-scrollbar-thumb {
    background: #e2b714;
}

::-webkit-scrollbar-thumb:hover {
    background: #f9e18b;
}

::-webkit-scrollbar-thumb:active {
    background: rgb(255, 200, 0);
}
</style>
